<template>

    <aside class="shadow-lg p-3 mb-5 bg-body rounded">
        <form @submit.prevent="addList()">
            <input type="text" v-model="newList">
            <input type="submit" value="Add list">
        </form>
        <ul>
            <li v-for="list in lists" 
                :key="list.id"
                :class="{'activeList': list.id === activeList}"
                @click="loadList(list.id)"
            >
                {{ list.name }}
               <span> <button @click.stop="removeList([list.id])">&times;</button> </span>
            </li>
        </ul>
    </aside>

</template>

<script>
import {mapState, mapActions} from 'vuex'

export default {
    name: 'Sidebar',
    props: {
        activeList: {type: Number}
    },
    computed: {
        ...mapState('todoList', ['lists']),
        newList: {
            get() {
                return this.$store.state.newList
            },
            set(value) {
                this.$store.commit('todoList/SET_NEW_LIST', value)
            }
        },
    },
    methods: {
        ...mapActions('todoList', ['addList','removeList']),
        loadList (id) {
            this.$emit('loadList', id)
        }
    }
}
</script>

<style scoped>

aside {
  grid-area: sidebar;
}
ul {
    list-style: none;
    padding: 0;
    
}
li{
   padding: 5px 5px 5px 5px;
   margin-top: 5px;
}
li span{
    float: right;
    margin-top: -3.5px;
}
li:hover {
    background: #eee;
}
.activeList {
    color: Tomato;
    font-weight: bold;
}
</style>